import React, { ReactElement, useCallback, useMemo, useContext } from 'react';
import styles from './css/dock.module.css';
import { TournamentStatus, TournamentSettings, Player } from '../interfaces';
import url from 'url';
import { MdExitToApp } from 'react-icons/md';
import {  FaTh, FaPauseCircle, FaPlayCircle, FaFilter, FaAngleDoubleDown, FaStickyNote, FaFileExport, FaLine, FaGripLines, FaSyncAlt, FaFileImport, FaCogs, FaBullhorn, FaBomb, FaArrowsAlt } from 'react-icons/fa';
import { LoadedContext, AlertContext } from '../Contexts';
import { TiFlowSwitch } from 'react-icons/ti';
import { POST, server } from '../utils/requests';

interface DockProps {
	userRound: number
	setRound: (round: number) => void
	togglePasteInput: () => void
	toggleFullScreen: () => void
	status: TournamentStatus
	settings: TournamentSettings
	updateTournamentStatus: () => void
	filterPairings: (choices: string[]) => void
	closeTournament: () => void
	toggleCrossTable: () => void
	toggleAnnouncements: (a: number) => void
	lastRoundOverride: boolean
	setLastRound: (override: boolean) => void
	triggerError: (message: string) => void
	switchPairings: (set?: boolean) => void
	switchMode: boolean
	load: () => void
	resetColumns: () => void
}

export default function Dock(props: DockProps): ReactElement {

	const round = useMemo(() => props.userRound || props.status.round, [props.userRound, props.status.round]);
	let lastRound = false;
	if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
	else lastRound = !props.status.active;
	const hasLoaded = useContext(LoadedContext);

	const { setAlert } = useContext(AlertContext);
	const undoRoundPairings = useCallback(() => {
		setAlert({
			type: 'confirm',
			message: 'Really undo all pairings for this round?',
			resolve: () => POST({
				url: url.resolve(server, ['tournament', props.status.id, 'undoRoundPairings'].join('/')),
				data: {
					round: props.status.round
				}
			}).then(() => {
				props.load();
				props.switchPairings();
			}),
		});
	}, [props.status.id, props.status.round, props.load, props.switchPairings]);

	const data = [
		{
			onClick: props.togglePasteInput,
			content: <FaFileImport style={{color: 'lightblue'}} />,
			name: 'Smart Import Players'
		},
		{
			name: 'Export Mode',
			content: <FaFileExport style={{color: 'lightblue'}} />,
			inactive: true
		},
		{
			name: 'Detailed Settings View',
			content: <FaCogs />,
			onClick: props.toggleFullScreen
		},			
		{
			name: 'Announcements',
			content: <FaBullhorn />,
			onClick: () => props.toggleAnnouncements(0),
			inactive: !props.settings.public
		},
		{
			name: 'Crosstable',
			onClick: props.toggleCrossTable,
			content: <FaTh style={{color: 'green'}} />
		},
		'divider',
		{
			name: lastRound ? 'Resume Tournament' : 'Pause Tournament',
			onClick: () => {
				if (lastRound && props.status.round >= props.settings.totalRounds) props.triggerError('Increase the number of rounds in Settings to continue!');
				else props.setLastRound(!lastRound);
			},
			content: <>
				{lastRound ? 
					<FaPlayCircle style={{color: 'yellow'}} /> :
					<FaPauseCircle style={{color: 'yellow'}} />
				}
			</>
		},
		{
			name: 'Refresh',
			content: <FaSyncAlt style={{color: 'lightblue'}} />,
			onClick: props.load,
			hidden: false
		},
		{
			name: !props.switchMode ? 'Switch or Undo Pairings' : 'Back',
			content: <>
				{!props.switchMode ? 
					<TiFlowSwitch style={{color: '#385898', strokeWidth: 1.5}} /> :
					<MdExitToApp style={{color: '#385898', strokeWidth: 1.5}} />
				}
			</>,
			active: props.switchMode,
			onClick: () => props.switchPairings(),
			inactive: round !== props.status.round || !props.status.round || !props.status.active
		},
		{
			name: 'Undo Last Round Pairings',
			content: <FaBomb style={{ color: 'indianred' }} />,
			onClick: undoRoundPairings
		},
		{
			name: 'Jump to outstanding Pairings',
			content: <FaAngleDoubleDown style={{color: 'lightgrey'}} />,
			inactive: true
		},
		{
			name: 'Filter Pairings',
			content: <FaFilter style={{color: 'darkgreen'}} />,
			inactive: true
		},
		{
			name: 'Player Details',
			content: <FaStickyNote style={{color: 'orange'}} />,
			inactive: true
		},
		{
			name: 'Reset layout',
			content: <FaArrowsAlt style={{ color: 'darkgrey' }} />,
			onClick: props.resetColumns
		},
		'divider',
		{
			name: 'Exit to Menu',
			content: <MdExitToApp style={{ color: 'indianred', strokeWidth: 1.5 }} />,
			onClick: props.closeTournament
		}
	];

	return (
		<div className={styles.dock}>
			<div className={[styles.stage, 'scrollable'].join(' ')}>
				<div className={[styles.column].join(' ')}>
					{data.map((b, i) => {
						if (typeof b === 'string') {
							return <div key={['divider', i].join('.')} className={styles.container}>
								<FaGripLines />
							</div>;
						} else {
							if (b.hidden) return null;
							return <div key={[b.name, i].join('.')} className={styles.container}>
								<div className={['button', styles.button, b.active ? styles.active : '', hasLoaded && b.inactive ? 'null' : ''].join(' ')} onClick={b.onClick}>
									{!hasLoaded? null : b.content}
								</div>
								<div className={styles.name}>
									{b.name}
								</div>
							</div>;
						}
					})}
				</div>
			</div>
		</div>
	);

}